<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      backgroundColor: val.bgColor,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import echarts from 'echarts/lib/echarts'
import dataControl from '../../CommonModule/mixins/dataControl'

const WIDGET_NAME = 'LinesAreaChart'
export default {
  name: WIDGET_NAME,
  group: 'chart',
  icon: require('./icon/thumb-lineschart.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 700,
    height: 350,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    chartTitle: '', // 
    textStyleColor: '#666666', // 
    itemStyleColor: '#42b983', // 
    xyturn: false, // xy
    titleColor: '#666666', // 
    xTextColor: '#666666', // X 
    yTextColor: '#666666', // Y 
    xLineColor: '#666666', // X 
    yLineColor: '#666666', // Y 
    xName: '', // x 
    yName: '', // x 
    xRotate: 0, // x 
    splitLineColor: '#3c4084', // y
    showXaxisTick: false, //  X 
    showYTick: false, //  Y 
    showXLine: true, //  X 
    showYLine: false, // Y 
    showYSplitLine: true, // Y 
    showXSplitLine: false, // X 
    showX: true, //  X 
    showY: true, //  Y 

    lgArr: [
      [{ // 
        color: '#9e1aa6',
        offset: 0
      }, {
        color: '#0000ff',
        offset: 1
      }],
      [{ // 
        color: '#f5e410',
        offset: 0
      }, {
        color: '#1c2ed0',
        offset: 1
      }],
      [{ // 
        color: '#f94f2b',
        offset: 0
      }, {
        color: '#882c8f',
        offset: 1
      }]
      // [{ // 
      //   color: '#42b983',
      //   offset: 0
      // }, {
      //   color: '#4255ff',
      //   offset: 1
      // }]
    ],
    xyArr: ['xAxis', 'yAxis'],

    gridTop: '70', // 
    gridLeft: '3%', // 
    gridRight: '8%', // 
    gridBottom: '3%', // 

    legendShow: true, // 
    legendWidth: '60%', // 
    legendHeight: '100', // 
    legendPositionX: 'center', // X 
    legendPositionY: 'top', // Y 
    legendFontSize: 12, // 
    legendTextColor: '#000000', // 
    legendIcon: 'roundRect', //  Icon  circle, rect, line, roundRect, triangle, diamond, pin, none
    legendIconWidth: 20, //  Icon 
    legendIconHeight: 10, //  Icon 
    legendIconGap: 10, //  Icon 

    seriesLineWidth: [2, 2, 2], // 
    seriesSymbol: ['none', 'none', 'none'], // 
    seriesSymbolSize: [5, 5, 5], // 
    isSmooth: [true, true, true], // 
    showTooltip: false, // 

    legend: [],

    dataAPI: 'https://easy-mock.com/mock/5c7ce20ccdc04f0e04185d9b/example/mock_copy_1560491079589/charts', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'dataseries', // 
    keyXAxis: 'categories', // x
    keyYAxis: 'statistics', // y

    dataLength: 3, // 
    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'dataseries': {
          'categories': ['2011', '2012', '2013', '2014 ', '2015', '2016', '2017'],
          'statistics': [
            {
              name: '',
              data: [320, 332, 301, 334, 390, 330, 320]
            },
            {
              name: '',
              data: [150, 232, 201, 154, 190, 235, 210]
            },
            {
              name: '',
              data: [178, 312, 260, 254, 290, 305, 290]
            }
          ]
        }
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      dynamicData: {}
    }
  },
  computed: {
    categories () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyXAxis]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyXAxis]
      } else {
        return []
      }
    },
    dataSeries () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyYAxis]) {
        const orign = this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyYAxis]
        if (this.$vpd.state.uuid === this.val.uuid) {
          let datalen = orign.length
          if (datalen > this.val.lgArr.length) {
            let params = {
              property: 'lgArr',
              data: [{
                color: datalen === 3 ? '#f94f2b' : '#42b983',
                offset: 0
              }, {
                color: datalen === 3 ? '#882c8f' : '#4255ff',
                offset: 1
              }]
            }
            let param = [
              {
                name: 'seriesLineWidth',
                value: 2
              },
              {
                name: 'seriesSymbol',
                value: 'none'
              },
              {
                name: 'seriesSymbolSize',
                value: 5
              },
              {
                name: 'isSmooth',
                value: true
              }
            ]
            this.$vpd.commit('UPDATE_DATAS_ADD', param)
            this.$vpd.commit('ADD_COLOR', params)
          } else if (datalen < this.val.lgArr.length) {
            let paramDel = {
              name: ['seriesLineWidth', 'seriesSymbol', 'seriesSymbolSize', 'isSmooth', 'lgArr'],
              value: datalen
            }
            this.$vpd.commit('UPDATE_DATAS_DEL', paramDel)
          }
          this.$vpd.commit('UPDATE_ACTIVE_ELEMENT', {
            name: 'dataLength',
            value: datalen
          })
        }

        return orign.map((item, i) => {
          return {
            type: 'line',
            name: item.name,
            itemStyle: {
              normal: {
                lineStyle: {
                  width: this.val.seriesLineWidth[i], // 
                  type: 'solid', // 
                  color: this.val.lgArr[i][0].color // 
                }
              }
            },
            symbol: this.val.seriesSymbol[i], //  'circle' | 'rectangle' | 'triangle' | 'diamond' | 'pin''arrow'
            symbolSize: this.val.seriesSymbolSize[i], // 
            smooth: this.val.isSmooth[i], // 
            areaStyle: {
              show: false,
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, this.val.lgArr[i]) // 
              }
            },
            data: item.data
          }
        })
      } else {
        return []
      }
    },
    legend () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyYAxis]) {
        // if (this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyYAxis]) {
        const orign = this.dynamicData[this.val.keyPrimary][this.val.keyTarget][this.val.keyYAxis]
        return orign.map(item => {
          return item.name
        })
      } else {
        return []
      }
    },
    xAxis () {
      return {
        name: this.val.xName,
        nameTextStyle: {
          color: this.val.xTextColor
        },
        type: 'category',
        nameLocation: 'end',
        show: this.val.showX, //  X 
        nameGap: '12',
        boundaryGap: false,
        axisTick: {
          show: this.val.showXaxisTick //  X 
        },
        axisLine: {
          show: this.val.showXLine, //  X 
          lineStyle: {
            color: this.val.xLineColor // X 
          }
        },
        splitLine: {
          show: this.val.showXSplitLine // X 
        },
        axisLabel: {
          interval: 0, // x
          rotate: this.val.xRotate, // x -90 ~ 90
          textStyle: {
            color: this.val.xTextColor // X 
          }
        },
        data: this.categories // 
      }
    },
    yAxis () {
      return { // 
        name: this.val.yName,
        nameTextStyle: {
          color: this.val.yTextColor
        },
        show: this.val.showY, //  Y 
        type: 'value',
        scale: true,
        inverse: false,
        // max: Math.max.apply(null, this.val.dataJSON.data.series),
        min: 0,
        axisLine: {
          show: this.val.showYLine, //  Y 
          lineStyle: {
            color: this.val.yLineColor // Y 
          }
        },
        axisLabel: {
          textStyle: {
            color: this.val.yTextColor // Y 
          }
        },
        axisTick: {
          show: this.val.showYTick, //  Y 
          alignWithLabel: this.val.yAxisLabel // Y 
        },
        splitLine: {
          show: this.val.showYSplitLine, //  Y 
          lineStyle: {
            type: 'solid',
            color: this.val.splitLineColor // y
          }
        },
        boundaryGap: [0.2, 0.2]
      }
    },
    options () {
      return {
        title: {
          text: this.val.chartTitle, // 
          textStyle: {
            color: this.val.titleColor
          }
        },
        color: this.val.lgArr.map((item, i) => {
          return item[0].color
        }),
        legend: {
          show: this.val.legendShow, // 
          x: this.val.legendPositionX,
          y: this.val.legendPositionY,
          width: this.val.legendWidth,
          // height: this.val.lengendHeight,
          icon: this.val.legendIcon, // 
          itemWidth: parseInt(this.val.legendIconWidth), // 
          itemHeight: parseInt(this.val.legendIconHeight), // 
          itemGap: parseInt(this.val.legendIconGap), // 
          textStyle: {
            color: this.val.legendTextColor,
            fontSize: this.val.legendFontSize
          },
          data: this.legend
        },
        tooltip: {
          show: this.val.showTooltip, // 
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        grid: {
          top: this.val.gridTop, // 
          left: this.val.gridLeft, //  
          right: this.val.gridRight, // 
          bottom: this.val.gridBottom, // 
          containLabel: true
        },
        xAxis: this.val.axisReverse ? this.yAxis : this.xAxis,
        yAxis: this.val.axisReverse ? this.xAxis : this.yAxis,
        series: this.dataSeries
      }
    }
  }
}
</script>
